 body {
        margin: 0;
        overflow: hidden;
        background: #111;
      }
      canvas {
        display: block;
      }

      /* === UI Bars === */
      #ui {
        position: absolute;
        top: 20px;
        left: 20px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        z-index: 10;
        font-size: 20px;
        font-family: sans-serif;
      }
      span{
        position: relative;
        left: 25px;
        color: white;
      }
      .bar {
        width: 20px;
        height: 150px;
        background: #444;
        border: 2px solid #888;
        position: relative;
      }
      .fill {
        height: 0%;
        background: limegreen;
        width: 100%;
        transition: width 0.1s linear;
        position: relative;
        bottom: 23px;
      }